@media (max-width: 1024px) {
    header {
        position: absolute;
        left: -300px;
        transition: all 600ms ease-out;
        background-color: #030440;
    }
    header.active {
        width: 50%;
        height: 100vh;
        left: 0;
        margin: 0;
        z-index: 1;
        transition: all 600ms ease-out;
    }
    .wrapper_menu>hr {
        margin-top: 150px;
    }
    .wrapper_notifications {
        width: 150px;
    }
    .wrapper_notifications>img {
        width: 30px;
        height: 30px;
        margin-right: auto;
        cursor: pointer;
    }
    .menu_burger {
        display: block;
        position: relative;
        width: 30px;
        height: 20px;
        float: right;
        cursor: pointer;
        z-index: 10;
    }
    .menu_burger span,
    .menu_burger::before,
    .menu_burger::after {
        position: absolute;
        width: 100%;
        height: 10%;
        transition: all 0.3s ease 0s;
        background-color: #54669C;
    }
    .menu_burger::before,
    .menu_burger::after {
        content: "";
    }
    .menu_burger::before {
        top: 0;
    }
    .menu_burger::after {
        bottom: 0;
    }
    .menu_burger span {
        top: 50%;
        -webkit-transform: scale(1) translate(0px, -50%);
                transform: scale(1) translate(0px, -50%);
    }
    .menu_burger.active span {
        -webkit-transform: scale(0) translate(0px, -50%);
                transform: scale(0) translate(0px, -50%);
    }
    .menu_burger.active::before {
        top: 50%;
        -webkit-transform: rotate(-45deg) translate(0px, -50%);
                transform: rotate(-45deg) translate(0px, -50%);
        background-color: #54669C;
    }
    .menu_burger.active::after {
        bottom: 50%;
        -webkit-transform: rotate(45deg) translate(0px, 50%);
                transform: rotate(45deg) translate(0px, 50%);
        background-color: #54669C;
    }
}

@media (max-width: 580px) {
    .search_message_notification {
        flex-direction: column;
        align-items: center;
    }
    form {
        max-width: 375px;
